.preload-images {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  background-image: url("../../Images/Wedding.jpg"),
    url("../../Images/Entertainment.jpg");
  visibility: hidden;
}
.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.background-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  transition: background-image 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  position: relative;
  text-align: center;
  color: white;
  z-index: 2;
  width: 90%;
  /* max-width: 800px; */
  padding: 20px;
  border-radius: 10px;
}

.button-header {
  font-size: 2.5rem;
  /* font-weight: 700; */
  margin-bottom: 20px;
  /* text-shadow: 2px 2px 10px rgba(140, 163, 236, 0.8), 0 0 15px rgba(0, 0, 255, 0.6); */
  font-family: "Poppins", sans-serif;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

.full-width-button {
  padding: 15px 20px;
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
  text-align: center;
  border-radius: 8px;
  transition: transform 0.3s ease, background 0.3s ease;
}

.full-width-button:hover {
  opacity: 100;
  color: rgb(160, 160, 128);
}

.svg-icon-container-home {
  position: absolute;
  top: 2%;
  left: 2%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 100px;
}

.svg-icon-home {
  width: 100%;
  height: 100%;
}

@media (min-width: 768px) {
  .button-container {
    flex-direction: row;
    justify-content: space-evenly;
    gap: 30px;
  }

  .button-header {
    font-size: 4rem;
  }

  .full-width-button {
    font-size: 1.5rem;
    padding: 20px 30px;
  }
}

@media (max-width: 767px) {
  .overlay {
    padding: 15px;
  }

  .button-header {
    font-size: 2rem;
  }

  .full-width-button {
    font-size: 1rem;
    padding: 12px 15px;
  }
}

@media (max-width: 480px) {
  .button-header {
    font-size: 1.5rem;
  }

  .full-width-button {
    font-size: 0.9rem;
    padding: 10px 12px;
  }

  .button-container {
    gap: 15px;
  }
}
